/* Fonts */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: black;
  color: white;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

/* Progress bar */

.progress-bar {
  width: 0%;
  height: 0.2em;
  position: fixed;
  top: 0;
  left: 0;
  /* background-color: #25F1E7; */
  background-color: #689af8;
  z-index: 110;
}

/* Navbar */

nav {
  position: relative;
  height: 10vh;
  padding: 10px 0;
  /* border-bottom: 1px solid black; */
  display: flex;
  flex-flow: row;
  justify-content: space-evenly;
  align-items: center;
  font-size: 1.1rem;
  color: hsla(0, 0%, 100%, 0.7);
  top: 0;
  left: 0;
  z-index: 100;
  background-color: rgb(36, 36, 36);
}

nav::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 35%;
  height: 13.2vh;
  background-color: black;
  z-index: -1;
  /* border-bottom: 1px solid salmon ; */
}

nav h1 {
  margin-right: 8em;
  color: white;
  text-transform: uppercase;
  font-family: "Courier New", Courier, monospace;
  letter-spacing: 0.01em;
  font-size: 1.8em;
  margin-top: 0.2em;
  position: relative;
  cursor: pointer;
}

nav h1::before {
  position: absolute;
  top: 1%;
  left: -6%;
  content: "";
  height: 1.1em;
  width: 0.15em;
  background-color: blueviolet;
  cursor: auto;
}

nav div {
  position: relative;
}

nav div:hover {
  color: #25f1e7;
  cursor: pointer;
}

nav div::after {
  content: "";
  position: absolute;
  color: #25f1e7;
  left: 0;
  bottom: -0.3em;
  width: 100%;
  height: 2px;
  background: currentColor;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin: right;
  transform-origin: right;
  transition: -webkit-transform 250ms ease-in;
  transition: transform 250ms ease-in;
  transition: transform 250ms ease-in, -webkit-transform 250ms ease-in;
}

nav div:hover::after {
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
  -webkit-transform-origin: left;
  transform-origin: left;
}

/* Main - Container */

.main-container {
  height: 90vh;
  padding: 2em 8em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1em solid rgb(36, 36, 36);
}

.main-container img {
  margin-right: 4em;
  opacity: 0.9;
}

.main-container div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main-container div h1 {
  font-size: 3.1em;
}

.main-container div p {
  font-family: "Poppins", sans-serif;
  margin-top: 3em;
  font-size: 1em;
  letter-spacing: 0.01em;
}

/* Work Experience */

.experience-container {
  min-height: 130vh;
  max-width: 100%;
  border-bottom: 1em solid rgb(36, 36, 36);
  padding-left: 5em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.experience-container h3 {
  color: #8491a0;
  font-size: 1.1em;
  letter-spacing: 0.02em;
}
.experience-container h1 {
  color: white;
  font-size: 2.5em;
  letter-spacing: 0.02em;
}

/* Experience Container > Work Container */

.work-container {
  display: flex;
  flex-direction: row;
  font-family: "Poppins", sans-serif;
}
.work-container .con {
  max-width: 27%;
  display: flex;
  flex-direction: column;
  padding-left: 2%;
  padding-right: 2%;
}
.exp-num {
  font-size: 6em;
  font-weight: 400;
  font-family: "Poppins", sans-serif;
  color: #8491a0;
}
.con h3 {
  font-size: 2em;
  color: white;
  letter-spacing: 0.02em;
}
.con div p {
  font-size: 1em;
  letter-spacing: 0.02em;
  margin-top: 2em;
}

/* Skill Container */

.skill-container {
  margin-top: 4em;
  /* height: 90vh; */
  width: auto;
  overflow: hidden;
  padding-bottom: 2rem;
  margin-bottom: 2em;
  border-bottom: 1em solid rgb(36, 36, 36);
}

.skill-1 {
  width: 50%;
  margin: auto auto 4em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Poppins", sans-serif;
  text-align: center;
}

.skill-1 h1 {
  font-weight: 600;
  font-size: 2.5em;
  letter-spacing: 0.02em;
  margin-bottom: 1em;
  position: relative;
}
.skill-1 h1::after {
  position: absolute;
  bottom: -0.1em;
  left: -50%;
  content: "";
  width: 200%;
  height: 0.1em;
  background-color: #8491a0;
}
.skill-1 p {
  color: #8491a0;
  font-size: 1.1rem;
}

.photobanner {
  width: 70%;
  position: relative;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}
