.photobanner svg {
  margin: 2em;
  height: 12vh;
  fill: white;
  transition: transform 0.5s;
  background: transparent;
}

.photobanner svg:hover {
  transform: scale(1.2);
}

/* certificates */

.certificate {
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-bottom: 1em solid rgb(36, 36, 36);
  padding-bottom: 2em;
}

.certificate h1 {
  font-weight: 600;
  font-size: 2.5em;
  letter-spacing: 0.02em;
  margin-bottom: 1em;
  position: relative;
}
.certificate h1::after {
  position: absolute;
  bottom: -0.1em;
  left: -50%;
  content: "";
  width: 200%;
  height: 0.1em;
  background-color: #8491a0;
}

.certificate div img {
  margin: 1.5em 2em;
  transition: transform 0.4s;
}

.certificate div img:hover {
  transform: scale(1.1) !important;
  box-shadow: 0px 5px 10px #689af8;
}
.amcat {
  width: 50%;
  font-size: 1em;
  height: 2em;
  padding: 10px 5px 0 5px;
  margin: 0.5em auto;
  border: 2px solid #689af8;
  text-align: center;
  font-weight: 600;
  border-radius: 8px;
  transition: 0.3s;
  cursor: pointer;
}
.amcat a {
  text-decoration: none;
  color: #8491a0;
}
.amcat:hover {
  transform: scaleX(1.05);
}
.amcat:hover a {
  color: white;
}

/* Contact Form */
.contact-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* border-bottom: 1em solid rgb(36, 36, 36); */
  padding-top: 1.5em;
  padding-bottom: 3em;
}
.contact-form div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  margin: 2em auto;
  text-align: center;
}
.contact-form form {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
  font-family: "Poppins", sans-serif;
  padding-bottom: 0.1em;
  border-bottom: 1px solid #689af8;
  width: 18%;
  transition: padding 0.5s;
}

.contact-form div h3 {
  font-size: 1em;
  text-transform: uppercase;
  letter-spacing: 0.02em;
  color: #689af8;
  margin: 0.5em;
}

.contact-form div h2 {
  font-size: 2.4em;
  letter-spacing: 0.02em;
  margin-bottom: 0.8em;
}

.contact-form div p {
  font-size: 1em;
  color: #8491a0;
  letter-spacing: 0.01em;
  font-family: "Poppins", sans-serif;
  margin-top: 1em;
  margin-bottom: 0.3em;
}

.contact-form form span {
  font-size: 14px;
  letter-spacing: 0.1em;
}

.contact-form form:hover {
  padding-left: 20px;
  background-color: #080135;
}

/* Footer */
footer {
  height: 20vh;
  background-color: #181823;
  padding-left: 2em;
  padding-top: 2em;
}

footer .abc {
  color: #8491a0;
  font-size: 1em;
  letter-spacing: 0.09em;
  margin-left: 4em;
  margin-bottom: 2em;
}

footer h1 {
  margin-right: 8em;
  color: white;
  text-transform: uppercase;
  font-family: "Courier New", Courier, monospace;
  letter-spacing: 0.01em;
  font-size: 1.8em;
  margin-top: 0.2em;
  position: relative;
  cursor: pointer;
}

footer h1::before {
  position: absolute;
  top: 1%;
  left: -10px;
  content: "";
  height: 1.1em;
  width: 0.15em;
  background-color: blueviolet;
  cursor: auto;
}

footer .logofooter {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  align-items: center;
}

.leetcode {
  background: white;
  /* margin-left: -0.5em;  */
  /* margin-top: -5px; */
  /* height: auto; */
  /* padding:  1px 1px -2px 1px;  */
  height: 4vh;
  margin-top: -5px;
  margin-right: 10px;
  margin-left: -10px;
  padding: 2px;
  cursor: pointer;
  border-radius: 1px;
}
.icon-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}
.leetcode:hover {
  background-color: #25f1e7;
}

footer .logofooter div a svg {
  margin-left: 1em;
  margin-right: 1em;
  fill: white;
}

footer .logofooter div a svg:hover {
  fill: #25f1e7;
}

/* Styles for tablets and iPads */
@media only screen and (min-device-width: 813px) and (max-device-width: 1023px) {
  /* Your styles for tablets and iPads go here */
  .main-container {
    height: 60vh;
  }
  .main-container div h1 {
    font-size: 2rem;
  }
  .main-container img {
    width: 300px;
    height: 300px;
  }
  .skill-container {
    height: auto;
  }
  .photobanner svg {
    height: 64px;
  }
  .contact-form form {
    width: 22%;
  }
}

@media only screen and (max-device-width: 812px) {
  .progress-bar {
    display: none;
  }
  /* Navbar */

  nav {
    box-sizing: border-box;
    position: absolute;
    height: 10vh;
    padding: 8px 0;
    font-size: 0.8rem;
    width: 100% !important;
  }

  nav::before {
    width: 50%;
  }

  nav h1 {
    margin-right: 0;
  }

  nav div {
    display: none;
  }

  /* Main - Container */

  .main-container {
    height: 70vh;
    padding: 1em 2em 2em 2em;
    margin-top: 10vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    border-bottom: 1em solid rgb(36, 36, 36);
    text-align: center;
  }

  .main-container img {
    margin-right: 0;
    width: 200px;
    height: 200px;
  }
  .main-container div h1 {
    font-size: 1.2em;
  }

  .main-container div p {
    margin-top: 2em;
    font-size: 0.8em;
    letter-spacing: 0.005em;
  }

  /* Work Experience */

  .experience-container {
    box-sizing: border-box;
    width: 100% !important;
    border-bottom: 1em solid rgb(36, 36, 36);
    padding-left: 0;
    text-align: center;
    align-items: center;
    margin-top: 20px;
  }
  .experience-container h3 {
    font-size: 0.8em;
  }
  .experience-container h1 {
    font-size: 1.8em;
    letter-spacing: 0.01em;
    margin-bottom: 0 !important;
  }

  /* Experience Container > Work Container */

  .work-container {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding-bottom: 2em;
  }
  .work-container .con {
    max-width: 80% !important;
    margin-top: 30px;
  }
  .exp-num {
    font-size: 3em;
  }
  .con h3 {
    font-size: 1.2em;
    letter-spacing: 0.01em;
  }
  .con div p {
    font-size: 0.8em;
    margin-top: 1em;
  }

  /* Skill Container */

  .skill-container {
    box-sizing: border-box;
    margin-top: 2em;
    height: auto;
    width: 100%;
    margin-bottom: 1em;
    border-bottom: 1em solid rgb(36, 36, 36);
    padding: 0 20px;
  }

  .skill-1 {
    width: 100%;
    margin: auto auto 2em;
    flex-direction: column;
  }

  .skill-1 h1 {
    font-size: 1.6em;
    letter-spacing: 0.01em;
    margin-bottom: 0.7em;
  }
  .skill-1 h1::after {
    display: none;
  }
  .skill-1 p {
    margin-bottom: 1em;
  }

  .photobanner {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(90px, 1fr));
    gap: 1.1rem;
    margin-bottom: 2rem;
  }

  .photobanner svg {
    margin: auto;
    height: 60px;
  }

  /* certificates */

  .certificate {
    width: 100%;
    margin: auto;
    box-sizing: border-box;
  }

  .certificate h1 {
    font-size: 1.6em;
    margin-bottom: 0.8em;
  }
  .certificate h1::after {
    display: none;
  }

  .certificate div img {
    margin: 1em 2.5em;
    width: 80% !important;
    height: auto !important;
  }

  .amcat {
    width: 60%;
  }

  /* Contact Form */
  .contact-form form {
    width: 80%;
  }

  /* Footer */
  footer {
    height: 24vh;
    padding-left: 0;
    padding-top: 1.5em;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  footer .abc {
    margin-left: 2em;
    margin-bottom: 1em;
  }

  footer h1 {
    margin-right: 0;
    font-size: 1.2em;
    margin-bottom: 0.5em;
  }

  footer .logofooter {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .icon-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 1em;
    margin-bottom: 5px;
  }

  footer .logofooter div a svg {
    margin-left: 10px;
    margin-right: 10px;
  }
}
